html, body {
    margin: 0;
    padding: 0;
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-color: #ffffff;
}

body {
    font-family: Arial, sans-serif;
}

#container {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    height: 100vh;
    aspect-ratio: 1;
    overflow: hidden;
}

#threejs-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 2;
    pointer-events: none;
}

#threejs-container canvas {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    max-height: none;
    max-width: none;
    object-fit: cover;
}

.cloudimage-360 {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: #000;
    z-index: 1;
}

.cloudimage-360 canvas {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: auto;
    height: auto;
    min-width: 100%;
    min-height: 100%;
    max-height: none;
    max-width: none;
    object-fit: cover;
}

.info-panel {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 80%;
    height: 80%;
    background: transparent;
    color: white;
    padding: 2%;
    border-radius: 12px;
    display: none;
    z-index: 1000;
    overflow: hidden;
}

#infoFrame {
    width: 100%;
    height: 100%;
    border: none;
    background: transparent;
}

.panel-visible {
    pointer-events: none;
}

.panel-visible .cloudimage-360,
.panel-visible #threejs-container {
    pointer-events: none;
}

.close-button {
    position: fixed;
    top: 2%;
    right: 2%;
    background: transparent;
    border: none;
    color: white;
    cursor: pointer;
    padding: 0;
    z-index: 1001;
    width: 6vw;
    height: 6vw;
    min-width: 50px;
    min-height: 50px;
    display: none;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    text-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.close-button svg {
    width: 50%;
    height: 50%;
    stroke: currentColor;
    filter: drop-shadow(0 0 2px rgba(0, 0, 0, 0.5));
}

.close-button:hover {
    color: #ff4444;
    text-shadow: 0 0 15px rgba(255, 68, 68, 0.5);
}

.close-button:hover svg {
    filter: drop-shadow(0 0 3px rgba(255, 68, 68, 0.5));
}

.info-panel h3 {
    margin-top: 0;
    font-size: 2.5vw;
    margin-bottom: 2%;
}

.info-panel p {
    font-size: 1.8vw;
    line-height: 1.5;
    margin: 0;
}